/**
 * SEO component that queries for data with
 *  Gatsby's useStaticQuery React hook
 *
 * See: https://www.gatsbyjs.com/docs/use-static-query/
 */

import * as React from "react"
import { Helmet } from "react-helmet"
import { useStaticQuery, graphql } from "gatsby"

import { ISite } from "@/definitions"
import { getTheme } from "@/utils/fns"

type MetaProps = JSX.IntrinsicElements["meta"]

export const Seo: React.FC<SEOProps> = ({ description, lang, meta, title }) => {
  const { site }: { site: ISite } = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            title
            description
            social {
              twitter
            }
          }
        }
      }
    `
  )

  const metaDescription = description || site.siteMetadata.description
  const defaultTitle = site.siteMetadata?.title

  const metaProps: MetaProps[] = [
    {
      name: `description`,
      content: metaDescription,
    },
    {
      property: `og:title`,
      content: title,
    },
    {
      property: `og:description`,
      content: metaDescription,
    },
    {
      property: `og:type`,
      content: `website`,
    },
    {
      name: `twitter:card`,
      content: `summary`,
    },
    {
      name: `twitter:creator`,
      content: site.siteMetadata?.social?.twitter || ``,
    },
    {
      name: `twitter:title`,
      content: title,
    },
    {
      name: `twitter:description`,
      content: metaDescription,
    },
  ]

  const className = getTheme()

  return (
    <Helmet
      htmlAttributes={{
        lang,
        class: className,
        ...(className === "dark" && { class: className }),
      }}
      title={title}
      titleTemplate={defaultTitle ? `%s | ${defaultTitle}` : undefined}
      meta={metaProps.concat(meta || [])}
    ></Helmet>
  )
}

interface SEOProps {
  description?: string
  lang?: string
  title: string
  meta?: MetaProps[]
}

Seo.defaultProps = {
  lang: `en`,
  meta: [],
  description: ``,
}
